import React, {useState} from 'react';
import Title from "../../../components/title";
import GridView from "../../../containers/grid-view";
import {KEYS} from "../../../constants/keys";
import {URLS} from "../../../constants/urls";
import {useTranslation} from "react-i18next";
import Select from "../../../components/select";
import {REPORT_TYPE_STATUS, REPORT_TYPES} from "../../../constants";
import {get, isEqual, keys} from "lodash";
import config from "../../../config";
import useAuth from "../../../hooks/auth/useAuth";
import dayjs from "dayjs";
import Badge from "../../../components/badge";


const ReportsContainer = () => {
    const {t} = useTranslation()
    const {token} = useAuth({});
    const [filter, setFilter] = useState({
        reportType: null, reportStatus: null
    });
    const columns = [{
        title: 'ID',
        key: 'id', classnames: '!w-[80px] !min-w-0'
    }, {
        title: t('File name'),
        key: 'file_name',
        render: ({row}) => isEqual(get(row,'status'),REPORT_TYPE_STATUS.success) ? <a className={'text-blue-700'}
                              href={`${config.FILE_URL}api/reports/${get(row, 'id')}?token=${token}`}
                              target={'_self'}>{get(row, 'file_name')}</a> : <span className={'text-red-600'}>{get(row,'file_name')}</span>
    }, {
        title: t('Report type'), key: 'type',
    },
        {
            title: t('Report status'),
            key: 'status',
            render:({value})=><Badge status={value}>{value}</Badge>
        },
        {
            title: t('Created date'),
            key: 'created_at',
            render: ({row}) => dayjs(get(row, 'created_at')).format("DD-MM-YYYY HH:mm:ss")
        }
    ]

    return (<div>
        <div className="grid grid-cols-12 items-center">
            <div className="col-span-12">
                <Title>{t("Reports")}</Title>
            </div>
            <div className="col-span-12 mt-8">
                <GridView
                    filters={<div className={'grid grid-cols-12 mb-5 gap-x-8'}>

                        <div className="col-span-3">
                            <Select
                                value={get(filter, 'reportType')}
                                setValue={(val) => setFilter((prev) => ({...prev, reportType: val}))}
                                label={t('Type')}
                                options={keys(REPORT_TYPES).map(_type => ({value: _type, label: t(_type)}))}/>
                        </div>
                        <div className="col-span-3">
                            <Select
                                value={get(filter, 'reportStatus')}
                                setValue={(val) => setFilter((prev) => ({...prev, reportStatus: val}))}
                                label={t('Status')}
                                options={keys(REPORT_TYPE_STATUS).map(_type => ({value: _type, label: t(_type)}))}/>
                        </div>
                    </div>}
                    listKey={KEYS.reports}
                    url={URLS.reports}
                    params={{status: get(filter, 'reportStatus.value'), type: get(filter, 'reportType.value')}}
                    columns={columns}/>
            </div>
        </div>
    </div>);
};

export default ReportsContainer;